<template>
  <toast-info class="container-fluid fs-normal" :data="info[binding][show.state]">
    <toast-button :data="{type:'tab',state:1,name:'',bi:'cursor-fill',toast:{type:'select',data:[{type:'amap',name:''},{type:'baidu',name:''}]}}" @run_toast="run_toast"/>
  </toast-info>
</template>

<script>
export default {
  props: {
    binding:'',
    data:{},
    show:{},
  },
  data(){
    return {
       info:{
         "p":{
           "2":{"title":",<span class='text-primary'></span>","text":""},
           "22":{"title":"","text":""},
           "31":{"title":"","text":""},
           "32":{"title":"<span class='text-black-50'></span>"+this.show.destination,"text":","+(this.show.ispay>0?'<span class="text-primary"></span>':'<span class="text-primary"></span>')},
           "33":{"title":"<span class='text-black-50'></span>"+this.show.destination,"text":","+(this.show.ispay>0?'<span class="text-primary"></span>':'<span class="text-primary"></span>')},
           "41":{"title":"<span class='text-black-50'></span>"+this.show.destination,"text":","+(this.show.ispay>0?'<span class="text-primary"></span>':'<span class="text-primary"></span>')},
           "42":{"title":"<span class='text-black-50'></span>"+this.show.destination,"text":","+(this.show.ispay>0?'<span class="text-primary"></span>':'<span class="text-primary"></span>')},
           "43":{"title":"<span class='text-black-50'></span>"+this.show.destination,"text":","+(this.show.ispay>0?'<span class="text-primary"></span>':'<span class="text-primary"></span>')},
           "51":{"title":"","text":","+(this.show.ispay>0?'<span class="text-primary"></span>':'<span class="text-primary"></span>')},
           "52":{"title":"","text":","+(this.show.ispay>0?'<span class="text-primary"></span>':'<span class="text-primary"></span>')},
           "53":{"title":"","text":","+(this.show.ispay>0?'<span class="text-primary"></span>':'<span class="text-primary"></span>')},
         }
       }
    }
  },
  methods:{
    run_toast(key){
      if(key.type==='amap'){
        this.$toast({type:'tips',data:{text:key.name}});
        this.callMap(this.show);
      }else if(key.type==='baidu'){
        this.$toast({type:'tips',data:{text:key.name}});
      }
    },
    ToggleAppAndH5( AppUrl , AppCallback = () => {}){
   // APP
  const ifr = document.createElement('iframe');
ifr.src = AppUrl;
ifr.style.display = 'none';
document.body.appendChild(ifr);
setTimeout(function(){
document.body.removeChild(ifr);
}, 3000);
// 800H5
let timer = setTimeout(function () {
clearTimeout(timer);
AppCallback();
}, 800);
window.onblur = function () {
clearInterval(timer);
};
    },
    Callback(key){
    frameDom.attr('src', "https://ditu.amap.com/dir?type=car&from[lnglat]="+ key.origin_lng +","+ ey.origin_lat +"&from[name]="+key.origin+"&to[lnglat]="+ key.destination_lng +","+ key.destination_lat +"&to[name]="+key.destination+"&src=com.youbei.chefu");
    },
    callMap(key) {
      const u = navigator.userAgent;
      const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
      let proto = isiOS ? 'iosamap://path' : 'amapuri://route/plan'  ;
      const AppUrl = proto + "?t= 0&slat="+key.origin_lat+"&slon="+key.origin_lng+"&sname="+key.origin+"&dlat="+key.destination_lat+"&dlon="+key.destination_lng+"&dname="+key.destination+"&src=xxx";
      this.ToggleAppAndH5(AppUrl,this.Callback(key))
    },
  }
}
</script>

<style>

</style>


