<template>
  <modal-center title="" @onclose="$emit('getinfo',{type:0})">
    <div v-if="data.binding=='p'">
        <div class="p-2">
            <div class="fw-bold py-2 fs-5"></div>  
            <div class="text-black-50">
                <div class="pt-1 text-rounded d-flex align-items-center"></div>
                <div class="pt-1 text-rounded d-flex align-items-center"><span class="text-danger">{{data.cancel}}</span></div>
            </div>
        </div>
        <div class="container-fluid p-2">
            <div class="row gx-2">
                <div class="col d-grid">
                    <button class=" btn btn-white border text-primary"  @click="$emit('getinfo',{type:1,data:data.form})"></button>
                </div>
                <div class="col d-grid">
                    <button class=" btn btn-primary"  @click="$emit('getinfo',{type:0})"></button>
                </div>
            </div>
        </div>
    </div>
    <div v-else-if="data.binding=='d'">
        <div class="p-2">
            <div class="fw-bold py-2 fs-5"></div>  
            <div class="text-black-50">
                <div class="pt-1 text-rounded d-flex align-items-center"></div>
                <div class="pt-1 text-rounded d-flex align-items-center"><span class="text-danger">{{data.dcancel}}</span></div>
            </div>
        </div>
        <div class="container-fluid p-2">
            <div class="row gx-2">
                <div class="col d-grid">
                    <button class=" btn btn-white border text-primary"  @click="$emit('getinfo',{type:1,data:data.form})"></button>
                </div>
                <div class="col d-grid">
                    <button class=" btn btn-primary"  @click="$emit('getinfo',{type:0})"></button>
                </div>
            </div>
        </div>
    </div>
</modal-center>
</template>
<script>
export default {
  props:{
    data:{}
  }
}
</script>

<style>

</style>


