<template>
  <div>
    <div class="text-rounded">
        <span class="ybefore">{{show.setdate|setdate(show.setdate_end)}}</span>
        <span class="ybefore">{{show.people}}</span>
        <span class="ybefore">{{show.carpooling==='1'?'':''}}</span>
    </div>
    <div class="d-flex justify-content-between align-items-end fs-normal">
        <div class="bd-highlight">
  
            <div class="ymerge">{{show.origin}}</div>
            <div class="ymerge">{{show.destination}}</div>
        </div>
        <div class="bd-highlight">
            <div v-if="show.state>22 && show.state<=53" class="text-center">
                <div><span class="fs-4">{{show.fee.passenger}}</span><span class="bi bi-chevron-right"></span></div>
            </div>
            <div v-else-if="show.carpooling==='0'" class="text-center">
                <div><span class="fs-4">{{show.price}}</span><span class="bi bi-chevron-right"></span></div>
            </div>
            <div v-else-if="show.carpooling==='1'" class="text-center">
                <div><span class="fs-4">{{show.p_price}}</span><span class="bi bi-chevron-right"></span></div>
                <div><span class="text-primary">{{show.n_price}}</span></div>
            </div>
        </div>
    </div>
    <div v-if="show.message" class="text-black-50">
        <div class="pt-1 text-rounded d-flex align-items-center" v-for="(item,index) in show.message.split(',')" :key="index" >{{names[item]}}</div>
    </div>
   </div>
</template>
<script>
export default {
  props: {
    set:{},
    data:{},
    show:{},
  },
  data(){
      return {
          names:['','','','/','','']
      }
  }
}
</script>

<style>

</style>


